import { request } from "../request"
function getUserById(userId) {
    let res = request(
      {
        url: "/user/information/info/" + userId,
        method: "get",
        data: {},
      }
    );
  
    return res;
  }

  function getStar(userId) {
    let res = request(
      {
        url: "/service/star/getStar/" + userId,
        method: "get",
        data: {},
      }
    );
  
    return res;
  }

  export {getUserById,getStar}