import { createRouter, createWebHistory } from 'vue-router'
import index from "../components/header.vue";
import main from "../index.vue";
import service_index from '../components/service/index.vue'
import service_infor from '../components/service/infor.vue'
import service_upload from '../components/service/upload.vue'
import service_type from '../components/service/type.vue'
import service_customisation from '../components/service/customisation.vue'



import login from '../components/user/login.vue'
import signup from '../components/user/signUp.vue'

import user_index from '../components/user/index.vue'
import profile from '../components/user/profile.vue'
import star from '../components/user/star.vue'
import message from '../components/user/message.vue'
import user_services from '../components/user/user_services.vue'
import user_tasks from '../components/user/user_tasks.vue'
import user_solutions from '../components/user/user_solutions.vue'

import help_index from '../components/help/index.vue'
import help_concept from '../components/help/concept.vue'
import help_feature from '../components/help/feature.vue'
import help_contribution from '../components/help/contribution.vue'
import help_development_team from '../components/help/development_team.vue'

const routes = [
  
  {
    path: '/',
    component: main
  },
    {
    path: '/service_index',
    component: index,
    children: [
      {
        path: '/service_index',
        name: 'service_index',
        component: service_index
      },
      {
        path: '/service_infor',
        name: 'service_infor',
        component: service_infor
      },
      {
        path: '/service_type',
        name: 'service_type',
        component: service_type
      },
      {
        path: '/service_upload',
        name: 'service_upload',
        component: service_upload
      },
      {
        path: '/service_customisation',
        name: 'service_customisation',
        component: service_customisation
      },
      {
        path:'/user',
        component:user_index,
        children: [
            {
              path: '/user',
              name: 'profile',
              component: profile
            },
            {
              path: '/user/star',
              name: 'star',
              component: star
            },
            {
              path: '/user/message',
              name: 'message',
              component: message
            },
            {
              path: '/user/services',
              name: 'services',
              component: user_services
            },
            {
              path: '/user/tasks',
              name: 'tasks',
              component: user_tasks
            },
            {
              path: '/user/solutions',
              name: 'solutions',
              component: user_solutions
            },

          ]
      },
      {
        path:'/help',
        component:help_index,
        children: [
          {
            path: '/help',
            name: 'concept',
            component: help_concept
          },
          {
            path: '/help/feature',
            name: 'feature',
            component: help_feature
          },
          {
            path: '/help/contribution',
            name: 'contribution',
            component: help_contribution
          },
          {
            path: '/help/development_team',
            name: 'development_team',
            component: help_development_team
          },
          
        ]
        }

    ]

  },
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/signup',
    name: 'signup',
    component: signup
  }
]

const router = createRouter({
  // mode:history,
   history: createWebHistory(),
  routes,
  
})


export default router
